  .header {
    box-sizing: border-box;
    overflow: hidden;
    background-image: url(/wwwreactapp/static/media/bg.4b20d07a.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    filter: blur(0);
    -webkit-filter: blur(8);
    position: relative;
    height: 100vh;
    max-width: 100%;
    width: 100vw;
    background-position: center;
    -webkit-animation: blureffect 3s;
            animation: blureffect 3s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;


  }

  @-webkit-keyframes blureffect {
    0% {
      -webkit-filter: blur(12);
              filter: blur(12);
    }

    10% {
      -webkit-filter: blur(10);
              filter: blur(10);
    }

    20% {
      -webkit-filter: blur(8px);
              filter: blur(8px);
    }

    40% {
      -webkit-filter: blur(5px);
              filter: blur(5px);
    }

    60% {
      -webkit-filter: blur(2px);
              filter: blur(2px);
    }

    80% {
      -webkit-filter: blur(1px);
              filter: blur(1px);
    }

    100% {
      -webkit-filter: blur(0px);
              filter: blur(0px);
    }

    100% {
      -webkit-filter: blur(0);
              filter: blur(0);
    }
  }

  @keyframes blureffect {
    0% {
      -webkit-filter: blur(12);
              filter: blur(12);
    }

    10% {
      -webkit-filter: blur(10);
              filter: blur(10);
    }

    20% {
      -webkit-filter: blur(8px);
              filter: blur(8px);
    }

    40% {
      -webkit-filter: blur(5px);
              filter: blur(5px);
    }

    60% {
      -webkit-filter: blur(2px);
              filter: blur(2px);
    }

    80% {
      -webkit-filter: blur(1px);
              filter: blur(1px);
    }

    100% {
      -webkit-filter: blur(0px);
              filter: blur(0px);
    }

    100% {
      -webkit-filter: blur(0);
              filter: blur(0);
    }
  }

  /* SCAN LINES */
  @-webkit-keyframes parpadeoScanlines {
    0% {
      opacity: 0.5;
    }

    50% {
      opacity: 0.8;
    }
  }
  @keyframes parpadeoScanlines {
    0% {
      opacity: 0.5;
    }

    50% {
      opacity: 0.8;
    }
  }

  .scanlines {
    pointer-events: none;
    -webkit-animation: 0.2s normal parpadeoScanlines infinite;
            animation: 0.2s normal parpadeoScanlines infinite;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0)), color-stop(50%, rgba(255, 255, 255, 0)), color-stop(70%, rgba(0, 0, 0, 0.2)), to(rgba(0, 0, 0, 0.6)));
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0) 50%, rgba(0, 0, 0, 0.2) 70%, rgba(0, 0, 0, 0.6));
    background-size: 100% 0.35rem;
    z-index: 2;
  }

  #overlayer {
    position: absolute;
    background-color: rgba(0, 0, 0, 1);
    width: 100vw;
    height: 100vh;
    z-index: 0;
    -webkit-animation: transparency .5s 1.5s forwards;
            animation: transparency .5s 1.5s forwards;
  }

  @-webkit-keyframes transparency {
    from {
      background-color: rgba(0, 0, 0, 1);
    }

    to {
      background-color: rgba(0, 0, 0, 0.3);
    }
  }

  @keyframes transparency {
    from {
      background-color: rgba(0, 0, 0, 1);
    }

    to {
      background-color: rgba(0, 0, 0, 0.3);
    }
  }

  header::before {
    content: " ";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(50%, transparentize(#121010, 1)), color-stop(50%, transparentize(darken(#121010, 10), 0.75))), -webkit-gradient(linear, left top, right top, from(transparentize(#ff0000, 0.94)), color-stop(transparentize(#00ff00, 0.98)), to(transparentize(#0000ff, 0.94)));
    background: linear-gradient(transparentize(#121010, 1) 50%, transparentize(darken(#121010, 10), 0.75) 50%), linear-gradient(90deg, transparentize(#ff0000, 0.94), transparentize(#00ff00, 0.98), transparentize(#0000ff, 0.94));
    z-index: 2;
    background-size: 100% 2px, 3px 100%;
    -webkit-animation: flicker 0.15s infinite;
            animation: flicker 0.15s infinite;

  }

  .fire {
    position: absolute;
    z-index: 5;
    width: 1280px;
    height: 720px;
    overflow: hidden;
    left: calc(50vw - 640px);
    top: calc(50vh - 360px);
    background-image: none;
  }

  .fire {
    z-index: 5;
    background-size: cover;
    background-image: url("http://00kaito.usermd.net/anim-opt.png");
    -webkit-animation: play 4.5s steps(49);
    animation: play 4.5s steps(49);
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;

  }

  .text {
    font-family: 'Righteous', cursive;

    color: #fff;
    display: block;
    --text-block-dim: 400px;
    height: var(--text-block-dim);
    width: var(--text-block-dim);
    margin: 0 auto;
    margin-top: calc(50vh - var(--text-block-dim)/2);
    position: relative;
  }

