  }

  @-webkit-keyframes play {
    from {
      background-position: 0px;
    }

    to {
      background-position: -62720px;
    }
  }


  /* GLITCH */
  .glitch {
    z-index: 2;
    position: absolute;
    top: 150px;
    line-height: 82%;
    font-size: 10rem;
    right: 0;
  }

  @-webkit-keyframes noise-anim {
    0% {
      clip: rect(50px, 100vw, 150px, 0);
    }

    1% {
      clip: rect(50px, 100vw, 150px, 0);
    }

    5% {
      clip: rect(50px, 100vw, 150px, 0);
    }

    6% {
      clip: rect(0, 100vw, 0, 0);

    }

    100% {
      clip: rect(0px, 100vw, 0px, 0);
    }

  }

  @keyframes noise-anim {
    0% {
      clip: rect(50px, 100vw, 150px, 0);
    }

    1% {
      clip: rect(50px, 100vw, 150px, 0);
    }

    5% {
      clip: rect(50px, 100vw, 150px, 0);
    }

    6% {
      clip: rect(0, 100vw, 0, 0);

    }

    100% {
      clip: rect(0px, 100vw, 0px, 0);
    }

  }

  .glitch:after {
    content: attr(data-text);
    position: absolute;
    left: 5px;
    text-shadow: -1px 0 red;
    color: white;
    background: black;
    overflow: hidden;
    clip: rect(0, 900px, 0, 0);
    animation: noise-anim-2 1.5s infinite linear alternate-reverse;
  }

  @-webkit-keyframes noise-anim-2 {
    0% {
      clip: rect(150px, 100vw, 200px, 0);
    }

    1% {
      clip: rect(150px, 100vw, 200px, 0);
    }

    5% {
      clip: rect(150px, 100vw, 200px, 0);
    }

    11% {
      clip: rect(0, 100vw, 0, 0);

    }

    25% {
      clip: rect(0, 100vw, 0, 0);
    }

    31% {
      clip: rect(100px, 100vw, 200px, 0);
    }

    35% {
      clip: rect(150px, 100vw, 200px, 0);
    }

    36% {
      clip: rect(100px, 100vw, 115px, 0);

    }

    40% {
      clip: rect(150px, 100vw, 170px, 0);

    }

    41% {
      clip: rect(0, 100vw, 0, 0);
    }

    100% {
      clip: rect(0, 100vw, 0, 0);
    }

  }

  @keyframes noise-anim-2 {
    0% {
      clip: rect(150px, 100vw, 200px, 0);
    }

    1% {
      clip: rect(150px, 100vw, 200px, 0);
    }

    5% {
      clip: rect(150px, 100vw, 200px, 0);
    }

    11% {
      clip: rect(0, 100vw, 0, 0);

    }

    25% {
      clip: rect(0, 100vw, 0, 0);
    }

    31% {
      clip: rect(100px, 100vw, 200px, 0);
    }

    35% {
      clip: rect(150px, 100vw, 200px, 0);
    }

    36% {
      clip: rect(100px, 100vw, 115px, 0);

    }

    40% {
      clip: rect(150px, 100vw, 170px, 0);

    }

    41% {
      clip: rect(0, 100vw, 0, 0);
    }

    100% {
      clip: rect(0, 100vw, 0, 0);
    }

  }

  .glitch:before {
    content: attr(data-text);
    position: absolute;
    left: -5px;
    color: white;
    background: black;
    overflow: hidden;
    clip: rect(0, 900px, 0, 0);
    animation: noise-anim 2s infinite linear alternate-reverse;
  }

